:root {
  --color-base: #ffffff;
}

.home {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: -100px;
}

.headerContainer {
  width: auto;
  margin-left: 70px;
}

.headerContainer h1 {
  justify-content: center;
  align-items: center;
  font-size: 90px;
  height: 60px;
  color: var(--color-base);
  font-weight: 50;    
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.5);
}

.headerContainer p {
  padding: 0;
  font-size: 38px;
  font-weight: lighter;
  color: var(--color-base); 
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
  margin-top: 3rem;
  margin-left: 5px
}

.headerContainer a {
  text-decoration: none;
}

.headerContainer button {
  background-color: rgb(0, 0, 0);  
  border-radius: 5px;
  cursor: pointer;
  justify-content: center;  
  height: 50px;
  width: 180px;
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  margin-top: 2rem;
  margin-left: 5px;
}

.headerContainer button:hover {
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  cursor: pointer;
  transition: 0.3s ease-in;
}

@media only screen and (max-width: 650px) {
  .home {
    justify-content: center;
    align-items: center;
  }  

  .headerContainer {
    margin-left: 0px;
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121619;
    color: white;
  }

  .headerContainer h1 {
    font-size: 40px;
    height: 30px;
    color: white;
  }

  .headerContainer p {
    font-size: 30px;
    color: white;
  }

  .headerContainer button {
    background-color: white;
    color: #121619;
    margin-bottom: 30px;
  }

  .headerContainer button:hover {
    background-color: rgb(225, 225, 225);
    color: #121619;
  }
}
